$bgColor: #d9d6da;
$white: #ffffff;
$brandGreen: #00ff87;
$brandBlue: #37003c;

body {
  font-family: 'Archivo', sans-serif;
}

.main-widget {
  width: 100%;
  //   display: flex;
  //   flex-direction: column;
  //   align-items: center;
  //   justify-content: center;
  //   color: $brandGreen;
  .content {
    height: 100vh;
    position: relative;
    .left {
      position: absolute;
      width: 50%;
      background-color: $brandGreen;
      height: 100%;
      border: 30px solid $white;
    }
    .rigth {
      position: absolute;
      width: 50%;
      background-color: #f8f8f8;
    }
    .title {
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      color: $brandBlue;
    }
  }
  h1 {
    padding: 0;
    margin: 0;
    font-size: 100px;
  }
}
